            </div>

            <div style="margin:5vh;"> 

                <div style="margin:20px;" v-show="wdHz">
                    <h5>System Parameters:</h5>
                    <p style="font-size:15px" class="small">
                    Natural Frequency, ω<sub>n</sub> (rad/s): {{ wn }} rad/s
                    <br>
                    Natural Frequency, ω<sub>n</sub> (Hz): {{ wnHz }} Hz
                    <br>
                    Damped Natural Frequency, ω<sub>d</sub> (rad/s): {{ wd }} rad/s 
                    <br>
                    Damped Natural Frequency, ω<sub>d</sub> (Hz): {{ wdHz }} Hz
                    </p>
                </div>

                <div style="margin:20px;" v-show="!wdHz">
                    <h5>System Parameters:</h5>
                    <p style="font-size:15px" class="small">
                    Natural Frequency, ω<sub>n</sub> (rad/s): {{ wn }} rad/s
                    <br>
                    Natural Frequency, ω<sub>n</sub> (Hz): {{ wnHz }} Hz
                    </p>
                </div>
            </div>

            <template #hotspots>
                <iv-pane position="left" @paneResize="update" @paneToggle="update">
                    <iv-sidebar-content style="font-size:12px" nextText="Vib. Iso." previousText="SDOF">
                        <iv-sidebar-section title="Forced Vibrations">
                            <img src="./assets/ForcedVib.png" class="centre" style="width:7vw;">
                            <p style="font-size:14px" class="small">By applying an excitation force, F(t), to a SDOF system, the system will experience forced vibrations.</p>
                            <p style="font-size:14px" class="small">Observe the system response to forced vibration by inputting the desired forcing amplitude, as well as the other input parameters of the system. You may adjust the slider to observe how the motion of the system varies with excitation frequency.</p>
                            <p style="font-size:14px" class="small">Assuming a sinusoidal harmonic excitation force at a frequency ω, the following equation of motion applies.</p>
                            <img src="./assets/EoMHistory.png" style="width:12vw;">
                            <p style="font-size:14px" class="small">The Frequency Response Function (FRF) is a representation of the response of a system to a given excitation force, consisting of magnitude and phase information at a range of excitation frequencies.</p>
                            <img src="./assets/EoMFRF.png" style="width:12vw;">
                            
                            <!-- This Forced Vibrations solver takes in your parameters and then produces an FRF response. You can then choose a frequency to view the time history plot at that specific frequency.
