                            <a href="https://forms.gle/W4DmoEKuGnu2RkWN6" target="_blank">here.</a>                   -->
                        </iv-sidebar-section>
                    </iv-sidebar-content>
                </iv-pane>

                <iv-pane position="right" :allowResize=false>
                    <div style="margin-top:0px; padding:20px;" >
                        <h5>Desired Plots</h5>
                        <form>
                            <p style="font-size:15px" class="small"><input type="radio" id="plot1" value="1" v-model="displaysplot" @change="update" checked>
                            <label for="plot1">Displacement (Receptance)</label>
                            <br>
                            <input type="radio" id="plot2" value="2" v-model="displaysplot" @change="update">
                            <label for="plot2">Velocity (Mobility)</label>
                            <br>
                            <input type="radio" id="plot3" value="3" v-model="displaysplot" @change="update">
                            <label for="plot3">Acceleration (Accelerance)</label></p>
                            
                        </form>
                        <!-- <h5>Desired FRF Plot</h5>
                        <form>
                            <input type="radio" id="plot1" value="1" v-model="displaysplot2" @change="update" checked>
                            <label for="plot1">Receptance</label>
                            <br>
                            <input type="radio" id="plot2" value="2" v-model="displaysplot2" @change="update">
                            <label for="plot2">Mobility</label>
                            <br>
                            <input type="radio" id="plot3" value="3" v-model="displaysplot2" @change="update">
                            <label for="plot3">Accelerance</label>
                            <br>
                        </form> -->
                        <h5>System Parameters</h5>
                        <p style="font-size:15px" class="small"><label>Mass, m (kg)</label>
                        <br>
                        <input v-model="mass" @change="update">
                        <br>
                        <label>Spring Constant, k (N/m)</label>
                        <br>
                        <input v-model="springConst"  @change="update">
                        <br>
                        <label>Use Damping Coefficient</label>
                        <br>
                        <iv-toggle-basic @input="dampToggle"></iv-toggle-basic>
                        <label v-show="!showCoeff" >Damping Ratio, ζ</label>
                        <label v-show="showCoeff">Damping Coefficient, c (Ns/m)</label>
                        <br>
                        <input v-model="dampRatio"  @change="updateDampRatio" v-show="!showCoeff">
                        <input v-model="dampCoeff"  @change="updateDampCoeff"  v-show="showCoeff"></p>

                        <h5>Initial Conditions</h5>
                        <p style="font-size:15px" class="small"><label>Initial Displacement, X0 (m)</label>
                        <br>
                        <input v-model="initDisp"  @change="update">
                        <br>

                        <label>Forcing Amplitude, F<sub>0</sub> (N)</label>
                        <br>
                        <input v-model="forceAmp"  @change="update"></p>
                        
                        <h5>Computational Parameters</h5>
                        <p style="font-size:15px" class="small"><label>ω Axis Limit, ω (Hz)</label>
                        <br>
                        <input v-model="wlim"  @change="update">
                        <br>
                        <label>Time Scale, t (s)</label>
                        <br>
                        <input v-model="timeSpan"  @change="update">
                        <br></p>

                        <br>
                    </div>
                </iv-pane>
                
                <!-- <iv-fixed-hotspot position="topright" style="width:300px; z-index:2" transparent>
                    
                </iv-fixed-hotspot> -->
            </template>

        
        </iv-visualisation>
    </div>
</template>
<script>
import Plotly from 'plotly.js/dist/plotly.min.js'; // eslint-disable-line no-unused-vars

export default {
    name:"App",
    data(){
        return {
            projectName: 'Vibrations App - Forced Vibrations',
            mass: 1,
            dampRatio: 0.1,
            initDisp:0.1,
            timeSpan:2,
            springConst: 1000,
            dampCoeff:6.325,
            numPoints:1000,
            wlim: 20,
            wSlider: 1,
            updatePlot: true,
            wn: 32,
            wnHz: 5,
            wd: 31,
            wdHz: 5,
            forceAmp: 5,
            maxAmp: 0.1,
            showText: true,
            showCoeff: false,
            displaysplot: "1",
            displaysplot2: "1"
        }
    },
    methods: {
        update(){
            if(this.mass <= 0){
                window.alert("Mass must be greater than 0.");
                this.mass = 1;
            }
            if(this.springConst <= 0){
                window.alert("Spring constant must be greater than 0.");
                this.springConst = 1000;
            }
            if(this.timeSpan <= 0){
                window.alert("Time span must be greater than 0.");
                this.springConst = 1000;
            }
            if(this.numPoints <= (2*this.wnHz * this.timeSpan)){
                window.alert(`Please ensure your sampling frequency (Number of Points/Time Span), ${this.numPoints/this.timeSpan} Hz, is well above double the natural frequency of the system, ${this.wnHz} Hz. This is to prevent aliasing from occuring. We recommend increasing the number of points beyond ${4*this.wnHz * this.timeSpan} points.`);
                this.numPoints = (4*this.wnHz * this.timeSpan);
            }
            if(this.wSlider > this.wlim){
                this.$refs.wSlider.reset();
            }
            if(this.wlim < 0.1){
                window.alert("Omega limit must be greater 0.1 Hz.");
                this.wlim = 0.1;
            }

            console.log('update');

            this.updatePlot = true;
        },
        updateSlider(e){
            this.wSlider = e;
            this.updatePlot = true;
        },
        updateDampCoeff(){
            if(this.dampCoeff < 0){
                window.alert("Damping coefficient must be greater than 0.");
                this.dampCoeff = 6.325;
            }
            this.dampRatio = Math.round( (this.dampCoeff / (2*Math.sqrt(this.springConst * this.mass))) *1000 )/1000;
            this.updatePlot = true;
        },
        updateDampRatio(){
            if(this.dampRatio < 0 || this.dampRatio > 2){
                window.alert("Damping ratio must be greater than 0 and less than or equal to 2.");
                this.dampRatio = 0.1;
            }
            this.dampCoeff = Math.round( (this.dampRatio *2 * Math.sqrt(this.springConst * this.mass) )*1000)/1000;
            this.updatePlot = true;
        },
        dampToggle(){
            this.showCoeff = !this.showCoeff;
        },
    },
    mounted(){
        let vm = this; // eslint-disable-line no-unused-vars

        let layout = {
            xaxis: {title:"Time (sec)"},
          yaxis: {
            title:"Displacement Response (m)",
          },
          yaxis2: {
            title:"Force Amplitude (m)",
            overlaying: 'y',
            side: 'right',
          },
          font: {
                color: "#003E74",
                weight: 900
          }
        };

        /* let layout3 = {
            xaxis: {title:"Time (sec)"},
          yaxis: {
            title:"Velocity Response (m/s)",
          },
          yaxis2: {
            title:"Force Amplitude (m)",
            overlaying: 'y',
            side: 'right',
          },
          font: {
                color: "#003E74",
                weight: 900
          }
        }; */

        let config = {responsive: true};

        function FRF_Solver(){

            let m = vm.mass;
            let k = vm.springConst;
            let dampRatio = Number(vm.dampRatio);
            let c = vm.dampCoeff;
            let wAxisLimit = vm.wlim;
            let wHz_axis = [];
            //let wHz = vm.wSlider;
            //let w = 2 * Math.PI * wHz;
            let amp = [];
            let phase = [];
            let mobility = [];
            let phase2 = [];
            let accelerance = [];
            let phase3 = [];

            /* let wn = Math.sqrt(k / m);  // Natural Freq of spring mass system
            let wd;
            if (0 < dampRatio && dampRatio < 1){
                 wd = wn*Math.sqrt(1 - dampRatio**2);    // Damped nat freq (rad/s)
            } else{
                wd = 0;
            } */

            c = dampRatio * 2 * Math.sqrt(k * m)
            
            // Currently, FRF amplitude (receptance) is being calculated as specified in the notes
            // Mobility is beind calculated by simply multiplying receptance by the current excitation frequency
            // Accelerance is receptance multipled by the square of excitation frequency
            // In reality, it may be that the CF is multiplied by the damped natural frequency and the steady state (PI) is multiplied by the excitation frequency
            // However, I haven't yet deduced which part of the formula is CF and which part is PI
            for(let i = 0; i < wAxisLimit; i = i + wAxisLimit/1000){
                wHz_axis.push(i);
                amp.push(1 / Math.sqrt((k - m * (2 * Math.PI * i) ** 2) ** 2 + (c * 2 * Math.PI * i) ** 2));
                mobility.push(i / Math.sqrt((k - m * (2 * Math.PI * i) ** 2) ** 2 + (c * 2 * Math.PI * i) ** 2));
                accelerance.push((i**2) / Math.sqrt((k - m * (2 * Math.PI * i) ** 2) ** 2 + (c * 2 * Math.PI * i) ** 2));

                phase.push((Math.atan2(-c * (2 * Math.PI * i), (k - m * (2 * Math.PI * i) ** 2)))*180/Math.PI);
                phase2.push((Math.atan2(-c * (2 * Math.PI * i), (k - m * (2 * Math.PI * i) ** 2)))*180/Math.PI + 90);
                phase3.push((Math.atan2(-c * (2 * Math.PI * i), (k - m * (2 * Math.PI * i) ** 2)))*180/Math.PI + 180);
            }

            return [wHz_axis, amp, phase, mobility, phase2, accelerance, phase3]
        }

        function forcedSolver(){
            
            let m = vm.mass;
            let k = vm.springConst;
            let dampRatio = Number(vm.dampRatio);
            let x0 = vm.initDisp;
            let Famp = vm.forceAmp;
            let wHz = vm.wSlider;
            let c = vm.dampCoeff;
            let w = 2 * Math.PI * wHz;
            let x = [];
            let t = [];
            let F = [];
            let d = vm.displaysplot;
            let d2 = vm.displaysplot2;

            let wn = Math.sqrt(k / m);  // Natural Freq of spring mass system
            let wnHz = wn/(2*Math.PI);    // Natural freq in Hz
            let wd, wdHz;
            if (0 < dampRatio && dampRatio < 1){
                 wd = wn*Math.sqrt(1 - dampRatio**2);    // Damped nat freq (rad/s)
                 wdHz = wd/(2*Math.PI);     // Damped Natural freq in Hz
            } else{
                wd = 0;
                wdHz = 0;
            }
            

            // Work out Nice time frame using decay to 10%
            // let t_decay = 1 / (dampRatio * wn) * Math.log(1 / 0.01);
            // let tend = t_decay * 1.25;
            let tend = vm.timeSpan;

            
            // Solving for Complete Forced Solution
            // Displacement amplitude from force ONLY
            let x0f = Famp / Math.sqrt((k - m * w ** 2) ** 2 + (c * w) ** 2);
            let phasef = Math.atan(c * w / (k - m * w ** 2));

            let A = x0 - x0f * Math.sin(-phasef);
            let B;
            if (0 < dampRatio  && dampRatio < 1){
                B = (dampRatio * wn * A - x0f * w * Math.cos(-phasef)) / wd;
            }else{
                B = 0;
            }

            /* // Trying to fix it
            A = (-c * w * Famp)/((k - m * w ** 2) ** 2 + (c * w) ** 2);
            
            if (0 < dampRatio  && dampRatio < 1){
                B = (-(m * w ** 2 - k) * Famp)/((k - m * w ** 2) ** 2 + (c * w) ** 2);
            }else{
                B = 0;
            }

            A = -x0f * Math.sin(phasef);
            B = x0f * Math.cos(phasef); */

            let vel = [];

            for(let i = 0; i < tend; i = i + tend/10000){
                t.push(i);
                //x.push(Math.exp(-dampRatio * wn * i) * (A * Math.cos(wd * i) + B * Math.sin(wd * i)) + x0f * Math.sin(w * i - phasef));
                x.push(Math.exp(-dampRatio * wn * i) * (A * Math.cos(wd * i) + B * Math.sin(wd * i)) );
                //vel.push(Math.exp(-dampRatio * wn * (i + (Math.PI/(2*wn)))) * (A * Math.cos(wd * (i + (Math.PI/(2*wn)))) + B * Math.sin(wd * (i + (Math.PI/(2*wn))))) + x0f * Math.sin(w * (i + (Math.PI/(2*wn))) - phasef));
                vel.push(Math.exp(-dampRatio * wn * (i + (Math.PI/(2*wn)))) * (A * Math.cos(wd * (i + (Math.PI/(2*wn)))) + B * Math.sin(wd * (i + (Math.PI/(2*wn))))) );
                F.push(Famp * Math.sin(w * i));
                
            }
            
            let acc = [];
            for(let i = 0; i < t.length; i++){
                vel[i] = vel[i] * wd
                acc[i] = - x[i] * wd**2
            }

            let max = x.reduce(function(a,b) {
                return Math.max(a,b)
            });
            vm.maxAmp = Math.round(max*100)/100;
            vm.wn = Math.round(wn*100)/100;
            vm.wnHz = Math.round(wnHz*100)/100;
            vm.wd =  Math.round(wd * 100)/100;
            vm.wdHz = Math.round(wdHz * 100)/100;    

            return [x, t, F, acc, vel, d, d2]
        }

        function update(){
            requestAnimationFrame(update);

            if(vm.updatePlot ){
                vm.updatePlot = false;
                
                let data = forcedSolver();
                let data2 = FRF_Solver();

                // Obtain min/max of amplitudes to set the range of values for the slider bar to be plotted
                let max1 = data2[1].reduce(function(a,b) {
                    return Math.max(a,b)
                });
                let min1 = data2[1].reduce(function(a,b) {
                    return Math.min(a,b)
                });

                let max2 = data2[3].reduce(function(a,b) {
                    return Math.max(a,b)
                });
                let min2 = data2[3].reduce(function(a,b) {
                    return Math.min(a,b)
                });

                let max3 = data2[5].reduce(function(a,b) {
                    return Math.max(a,b)
                });
                let min3 = data2[5].reduce(function(a,b) {
                    return Math.min(a,b)
                });

                // Obtain the maximum v and a to set appropriate axes ranges for the 0 lines to be the same
                let absx = [];
                let absv = [];
                let absa = [];
                for (let i = 0; i < data[1].length; i++) {
                    absx.push(Math.abs(data[0][i]));
                    absv.push(Math.abs(data[4][i]));
                    absa.push(Math.abs(data[3][i]));
                }
                //let maxx = absx.reduce(function(a, b) {
                //    return Math.max(a, b);
                //}, -Infinity);
                let maxv = absv.reduce(function(a, b) {
                    return Math.max(a, b);
                }, -Infinity);
                let maxa = absa.reduce(function(a, b) {
                    return Math.max(a, b);
                }, -Infinity);

                // Redefine layout to keep axes ranges the same
                layout = {
                    xaxis: {title:"Time (sec)"},
                    yaxis: {
                        title:"Displacement Response (m)",
                        range: [-1.1 * vm.maxAmp, 1.1 * vm.maxAmp],
                    },
                    yaxis2: {
                        title:"Force Amplitude (m)",
                        overlaying: 'y',
                        side: 'right',
                        range: [-1.1 * vm.forceAmp, 1.1 * vm.forceAmp],
                        showgrid: false,
                    },
                    font: {
                            color: "#003E74",
                            weight: 900
                    },
                    legend: {
                        bgcolor: 'rgba(0,0,0,0.2)',
                        x: 1,
                        xanchor: 'right',
                        y: 1.2
                    }
                };
                
                let layout3 = {
                    xaxis: {title:"Time (sec)"},
                    yaxis: {
                        title:"Velocity Response (m/s)",
                        range: [-1.1 * maxv, 1.1 * maxv],
                    },
                    yaxis2: {
                        title:"Force Amplitude (m)",
                        overlaying: 'y',
                        side: 'right',
                        range: [-1.1 * vm.forceAmp, 1.1 * vm.forceAmp],
                        showgrid: false,
                    },
                    font: {
                            color: "#003E74",
                            weight: 900
                    },
                    legend: {
                        bgcolor: 'rgba(0,0,0,0.2)',
                        x: 1,
                        xanchor: 'right',
                        y: 1.2
                    }
                };

                let layout4 = {
                    xaxis: {title:"Time (sec)"},
                    yaxis: {
                        title:"Acceleration Response (m/s<sup>2</sup>)",
                        range: [-1.1 * maxa, 1.1 * maxa],
                    },
                    yaxis2: {
                        title:"Force Amplitude (m)",
                        overlaying: 'y',
                        side: 'right',
                        range: [-1.1 * vm.forceAmp, 1.1 * vm.forceAmp],
                        showgrid: false,
                    },
                    font: {
                            color: "#003E74",
                            weight: 900
                    },
                    legend: {
                        bgcolor: 'rgba(0,0,0,0.2)',
                        x: 1,
                        xanchor: 'right',
                        y: 1.2
                    }
                };

                let layout2 = {
                    xaxis: {title:"Excitation frequency (Hz)"},
                yaxis: {
                    title:"Receptance, x/F (m/N)",
                    type: 'log'
                },
                yaxis2: {
                    title:"Phase (Degrees)",
                    overlaying: 'y',
                    side: 'right',
                    showgrid: false,
                },
                font: {
                        color: "#003E74",
                        weight: 900
                },
                legend: {
                    bgcolor: 'rgba(0,0,0,0.2)',
                    x: 1,
                    xanchor: 'right',
                    y: 1.2,
                }
                };

                let layout5 = {
                    xaxis: {title:"Excitation frequency (Hz)"},
                yaxis: {
                    title:"Mobility, x&#775;/F (m/Ns)",
                    type: 'log'
                },
                yaxis2: {
                    title:"Phase (Degrees)",
                    overlaying: 'y',
                    side: 'right',
                    showgrid: false,
                },
                font: {
                        color: "#003E74",
                        weight: 900
                },
                legend: {
                    bgcolor: 'rgba(0,0,0,0.2)',
                    x: 1,
                    xanchor: 'right',
                    y: 1.2,
                }
                };

                let layout6 = {
                    xaxis: {title:"Excitation frequency (Hz)"},
                yaxis: {
                    title:"Accelerance, &#7821;/F (m/Ns<sup>2</sup>)",
                    type: 'log'
                },
                yaxis2: {
                    title:"Phase (Degrees)",
                    overlaying: 'y',
                    side: 'right',
                    showgrid: false,
                },
                font: {
                        color: "#003E74",
                        weight: 900
                },
                legend: {
                    bgcolor: 'rgba(0,0,0,0.2)',
                    x: 1,
                    xanchor: 'right',
                    y: 1.2,
                }
                };

                // Plot the graph, checking with displaysplot2 to display the selected plot

                if (data[5] == "1"){
                    Plotly.newPlot('graph', [{
                        x: data2[0],
                        y: data2[1],
                        line: {simplify: false},
                        mode: 'lines',
                        name: "FRF Amplitude"
                    },
                    {
                        x: data2[0],
                        y: data2[2],
                        line: {simplify: false},
                        mode: 'lines',
                        name: "FRF Phase", 
                        yaxis: 'y2'
                    },
                    {
                        x: [vm.wSlider, vm.wSlider],
                        y: [min1, max1],
                        mode: 'lines',
                        showlegend: false,
                        line: {
                            dash: 'dashdot',
                            color: 'red'
                        }

                    }],
                    layout2,
                    config
                    )
                }
                
                if (data[5] == "2"){
                    Plotly.newPlot('graph', [{
                        x: data2[0],
                        y: data2[3],
                        line: {simplify: false, color: 'rgb(152,66,245)'},
                        mode: 'lines',
                        name: "FRF Amplitude"
                    },
                    {
                        x: data2[0],
                        y: data2[4],
                        line: {simplify: false},
                        mode: 'lines',
                        name: "FRF Phase", 
                        yaxis: 'y2'
                    },
                    {
                        x: [vm.wSlider, vm.wSlider],
                        y: [min2, max2],
                        mode: 'lines',
                        showlegend: false,
                        line: {
                            dash: 'dashdot',
                            color: 'red'
                        }

                    }],
                    layout5,
                    config
                    )
                }

                if (data[5] == "3"){
                    Plotly.newPlot('graph', [{
                        x: data2[0],
                        y: data2[5],
                        line: {simplify: false, color: 'rgb(101,201,104)'},
                        mode: 'lines',
                        name: "FRF Amplitude"
                    },
                    {
                        x: data2[0],
                        y: data2[6],
                        line: {simplify: false},
                        mode: 'lines',
                        name: "FRF Phase", 
                        yaxis: 'y2'
                    },
                    {
                        x: [vm.wSlider, vm.wSlider],
                        y: [min3, max3],
                        mode: 'lines',
                        showlegend: false,
                        line: {
                            dash: 'dashdot',
                            color: 'red'
                        }

                    }],
                    layout6,
                    config
                    )
                }

                //3
                
                if (data[5] == "1"){
                    Plotly.newPlot('graph2', [
                    {
                    x : data[1],
                    y : data[0],
                    line: {simplify: false},
                    mode: 'lines',
                    name: "Displacement Response, x"
                    },
                    {
                    x : data[1],
                    y : data[2],
                    line: {simplify: false},
                    mode: 'lines',
                    name: "Force, F",
                    yaxis: 'y2'
                    }
                    ],
                    layout,
                    config
                    )
                }

                if (data[5] == "2"){
                    Plotly.newPlot('graph2', [
                    {
                    x : data[1],
                    y : data[4],
                    line: {simplify: false, color: 'rgb(152,66,245)'},
                    mode: 'lines',
                    name: "Velocity Response, v"
                    },
                    {
                    x : data[1],
                    y : data[2],
                    line: {simplify: false},
                    mode: 'lines',
                    name: "Force, F",
                    yaxis: 'y2'
                    }
                    ],
                    layout3,
                    config
                    )
                }

                if (data[5] == "3"){
                    Plotly.newPlot('graph2', [
                    {
                    x : data[1],
                    y : data[3],
                    line: {simplify: false, color: 'rgb(101,201,104)'},
                    mode: 'lines',
                    name: "Acceleration Response, a"
                    },
                    {
                    x : data[1],
                    y : data[2],
                    line: {simplify: false},
                    mode: 'lines',
                    name: "Force, F",
                    yaxis: 'y2'
                    }
                    ],
                    layout4,
                    config
                    )
                }
                
            }

        }

        update();
    }
}
</script>
